html{
    background-color: #FFF;
    background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), 
    url('/assets/images/dark-geometric.png')
}

nav .link{
    font-family: 'Chakra Petch', sans-serif;
    font-weight: 600;
    margin-right:30px;
    font-size: 30px;
    text-decoration: none;
    color: #000E8E;
    position: absolute;
    right: 10px;
    top: 37px;
}

nav header{
    font-family: 'Chakra Petch', sans-serif;
    font-size: 42px;
    font-weight: 600;
    color: white;
    position: relative;
    left: 40px;
    top:25px;
    display: inline-block;
}
nav header::after{
    content:"";
    background-color: #000E8E;
    width: 590px;
    height: 61px;
    display: block;
    top: -2px;
    left: -50px;
    position: absolute;
    z-index: -1;
}

a .blog-link{
    text-decoration: none;
    color: black;
}

nav{
    margin-bottom: 70px;
}

h1{
    font-family: 'Chakra Petch', sans-serif;
    font-size: 34px;
    color: #000E8E;
    text-decoration: underline;
    text-transform: uppercase;
    letter-spacing: 5px;
    text-align: center;

}
h2{
    font-family: 'Chakra Petch', sans-serif;
    font-size: 23px;
    color: #000E8E;
    text-decoration: underline;
    text-transform: uppercase;
}
p{
    font-size: 21px;
    font-family: 'Archivo', sans-serif;
}
 

.blog-link{
    height: 130px;
    width: 1200px;
    margin-bottom: 70px !important;

    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.1);
    margin: 0 auto;
    position: relative;
    cursor: pointer;
}
.blog-link:hover{
    background-color:rgba(0,0,0,0.2);
}

.blog-link::after{
    /* credit to https://css-tip.com/corner-only-border-image/ for border */
    --s: 50px; /* the size on the corner */
    --t: 3px;  /* the thickness of the border */
    --g: 10px; /* the gap between the border and image */
    
    border-radius: 20px;
    content: "";
    display: block;
    height: 130px;
    width: 1200px;
    position: absolute;
    top: -13px;
    left: -13px;
    padding: calc(var(--g) + var(--t));
    outline: var(--t) solid rgba(0,0,0,0.2); /* the color here */
    outline-offset: calc(-1*var(--t));
    -webkit-mask:
        conic-gradient(at var(--s) var(--s),#0000 75%,#000 0)
        0 0/calc(100% - var(--s)) calc(100% - var(--s)),
        linear-gradient(#000 0 0) content-box;
    transition: .1s;
}
.blog-link:hover:after {
    outline-offset: calc(var(--g)/-1);
}

.blog-link .blog-img{
    height: 129px;
    width: 139px;
    border-radius: 10px 0 0 10px;
    border: 1px solid black;
    object-fit: cover;
}
.blog-link .side{
    display:inline-block;
    vertical-align: top;
}
.blog-link .text{
    position: relative;
    width: 1000px;
    margin-left:20px;
    height: inherit;
}

.blog-link .blog-link-title{
    font-weight: 600;
    margin: 0;
}
.blog-link .blog-link-body{
    margin: 0;
}
.blog-link .blog-bottom{
    position: absolute;
    bottom: 5px;
    width: 100%;
}
.blog-link .blog-bottom div{
    display: inline-block;
}
.blog-link .blog-bottom .more{
    float: right;
}
.blog-link .blog-bottom .date{
    color: #6A5858;
}
.blog-post{
    width: 680px;
    padding: 20px 50px;

    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.1);
    margin: 0 auto;
    position: relative;
}

.blog-post .hero{
    width: calc(100% + 100px);
    position: relative;
    left:-50px;
    top: -20px;
    border-radius: 10px 10px 0 0;
}

.blog-post .blog-img{
    width: 70%;
    display: block;
    margin: 0 auto;
    border-radius: 10px;
}
.blog-post figcaption{
    color: #6A5858;
    font-size: 19px;
    margin-top: 3px;
    font-family: 'Archivo', sans-serif;
    text-align: center;
}

.blog-post h1{
    margin-top: 0;
}

.blog-post p{
    line-height:32px;
}
figure{
    margin: 0;
}

ul{
    padding-left: 0;
}

// sizes for smaller / mobile devices
@media only screen and (max-width: 1250px) {
    h1{
        font-size: 23px;
        letter-spacing: 5px;
    
    }
    h2{
        font-size: 18px;
    }
    p{
        font-size: 16px;
    }

    nav header{
        // display: block;
        // font-size: 26px;
        position: initial;
        width: 100%;
        // height: 31px;
        text-align: center;
        background-color: #000E8E;
    }
    nav header::after{
        all:unset;
    }
     


    .blog-link{
        height: unset;
        width: 100%;
        margin-bottom: 70px !important;
    
        border-radius: 10px;
        background-color: rgba(0, 0, 0, 0.1);
        margin: 0 auto;
        position: relative;
        cursor: pointer;
    }
    .blog-link .text{
        width: calc(100% - 20px);
        margin-left: 0;
        padding: 10px;
    }
    .blog-link .img{
        width: 100%;
    }
    .blog-link .blog-img{
        width: calc(100% - 2px);
        height: unset;
        // display: block;
        // margin: 0 auto;
        border-radius: 10px 10px 0 0;
        max-height: 60vh;
        object-fit: cover;
    }

    .blog-link::after{
        all:unset;
    }
    .blog-link .blog-bottom{
        margin-top:10px;
        position: relative;
    }
    .blog-link h2{
        font-size:30px;
    }
    .blog-link p{
        font-size: 22px;
    }

    nav .link{
        display: none;
    }
  }
@media only screen and (max-width: 800px) {
    .blog-post{
        width: calc(100% - 10px);
        padding: 5px;
    }

    .blog-post .hero{
        width: calc(100% + 10px);
        position: relative;
        left: -5px;
        top: -5px;
    }

    .blog-post figcaption{
        font-size: 14px;
    }
    .blog-post .blog-img{
        width: 100%;
    }
}

#about-text{
    font-size: 30px;
    margin-top: 150px;
    margin-left: 50px;
}
#about-table{
    border-spacing: 25px 0;
    margin-left:25px;
}
#about-table img{
